.container{
     display: flex;
     flex-direction: column;
     align-items: center;
     margin: auto;
     margin-top: 150px;
     background: #625ac4;
     width: 800px;
     border-radius: 15px;
}
.quote{
    padding: 60px 110px;
    color: white;
    font-size: 32px;
    text-align: center;

} 
.line{
    width: 680px;
    height: 1.5px;
    background: white;
}
.bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 30px 0px;
}
.author{
    color: white;
    font-size: 20px;
    font-weight: 500;
}
.icons{
    display: flex;
    gap: 40px;

}
.icons img{
    cursor: pointer;
    height: 36px;
}